al. Implica fortalecer la producción y agregarle valor para posicionar los productos primero en el ámbito local y posteriormente en los mercados nacional e internacional. Pensar global y actuar local también requiere identificar y potenciar las fortalezas del territorio, como el saber hacer tradicional, la capacidad asociativa o la existencia de liderazgos sólidos que guíen el trabajo comunitario. En conjunto, este principio fomenta una actitud abierta al aprendizaje y a la innovación, sin desvincularse del sentido de pertenencia territorial.")])]),_c('div',{staticClass:"col-lg-5 col-10 order-lg-2 order-1 mb-lg-0 mb-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img11.jpg"),"alt":"Imagen decorativa"}})])])]),_c('div',{staticClass:"row justify-content-center p-4"},[_c('div',{staticClass:"col-lg-7 order-lg-1 order-2"},[_c('h4',[_vm._v("Desarrollo del recurso humano")]),_c('p',[_vm._v("El enfoque OVOP reconoce que el principal motor del desarrollo no son los recursos materiales, sino las personas. Por ello, este principio se orienta al fortalecimiento de las capacidades individuales y colectivas, promoviendo la formación, el liderazgo y la organización comunitaria. El desarrollo del recurso humano implica mejorar los conocimientos técnicos, las habilidades de gestión y las competencias de liderazgo necesarias para impulsar el enfoque. La existencia de líderes comprometidos, capaces de comunicar, motivar y guiar a otros hacia un objetivo común, resulta esencial para consolidar procesos sostenibles de desarrollo local.")])]),_c('div',{staticClass:"col-lg-5 col-10 order-lg-2 order-1 mb-lg-0 mb-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img12.jpg"),"alt":"Imagen decorativa"}})])])])])],1),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("En síntesis, los tres principios de OVOP, autogestión y creatividad, pensar global y actuar local, y desarrollo del recurso humano, constituyen la base que orienta el trabajo comunitario y garantizan la sostenibilidad del enfoque en los territorios.")]),_c('div',{staticClass:"row justify-content-center mb-0"},[_c('div',{staticClass:"col-lg-10 col-12"},[_c('div',{staticClass:"tarjeta tarjeta--C02 p-4",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(19),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_vm._m(20),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton color-acento-botones texto-blanco",attrs:{"href":_vm.obtenerLink('/downloads/DE_Estrategia_de_Desarrollo_Local_Incluyente_OVOP_Colombia.pdf'),"target":"_blank"}},[_c('span',[_vm._v("Descargar")]),_c('i',{staticClass:"fas fa-file-download"})])])])])])])])])],1)],1)
